import React, { useState } from 'react';

const About = () => {
     const code = `import requests\n\nurl = "https://www.纯度.site/run"\nparams = {\n    "text": "你要合成的文字",\n    "id_speaker": 119,\n    "length": 1,\n    "noise": 0.2,\n    "noisew": 0.37\n}\n\nresponse = requests.get(url, params=params)\nif response.status_code == 200:\n    with open("output.wav", "wb") as f:\n        f.write(response.content)\n        print("语音文件保存成功")\nelse:\n    print("请求失败")`;
     const code2 = `import requests\n\nurl = "https://www.纯度.site/models"\n\nresponse = requests.get(url)\nif response.status_code == 200:\n    models = response.json()\n    print("模型列表:")\n    for model in models:\n        print(model)\nelse:\n    print("请求失败")`;

  const [copied, setCopied] = useState(false);

  const copyCode = () => {
    navigator.clipboard.writeText(code);
    setCopied(true);
  };
    return (
        <div>
            <div className="row">
                {/*<div className="text-center">*/}
                <ul>
                    <li>
                        <h4>1. 文字合成语音接口</h4>
                    <p>URL: <code>www.纯度.site/run</code></p>
                    </li>
                    <li>
                    <p>方法: GET</p>
                    </li>
                    <li>
                    <p>参数:</p>
                    <ul>
                    <li><code>text</code> (必需): 要合成的文字内容，最多250字。</li>
                    <li><code>id_speaker</code> (可选): 说话人的ID，默认为119。</li>
                    <li><code>length</code> (可选): 合成语音的长度，值越大合成时间越长，默认为1.1。</li>
                    <li><code>noise</code> (可选): 情感起伏大小，值越大情感起伏越明显，默认为0.2。</li>
                    <li><code>noisew</code> (可选): 音素发音长度，值越大发音越长，默认为0.37。</li>
                    </ul>
                    </li>
                    <li>
                    <p>返回结果: 合成的语音文件。</p>



                    <p>Python调用案例:
    <div className="container mt-5">
      <div className="row">
        <div className="col-md-8">
          <textarea className="form-control" value={code} rows={7} readOnly />
        </div>
        <div className="col-md-4">
          <button
            className="btn btn-primary"
            onClick={copyCode}
            disabled={copied}
          >
            {copied ? '已复制' : '复制代码'}
          </button>
        </div>
      </div>
    </div>
     </p>
  </li>
                      </ul>
                    <h4 className="py-4">2. 获取模型列表接口</h4>
                    <ul>
                        <li>
                        <p>URL: <code>www.纯度.site/models</code></p>
                        </li>
                        <li>
                        <p>方法: GET</p>
                        </li>
                        <li>
                        <p>返回结果: 模型列表。</p>
                        </li>
                        <li>
                        <p>Python调用案例:
                                <div className="container mt-5">
      <div className="row">
        <div className="col-md-8">
          <textarea className="form-control" value={code2} rows={7} readOnly />
        </div>
        <div className="col-md-4">
          <button
            className="btn btn-primary"
            onClick={copyCode}
            disabled={copied}
          >
            {copied ? '已复制' : '复制代码'}
          </button>
        </div>
      </div>
    </div>
     </p>

                        </li>
                        </ul>


                {/*</div>*/}
            </div>
        </div>
    );
};

export default About;